import { Dialog } from 'vant';
import { Toast } from 'vant';
export default {
    data() {
        return {
            attr: {
                show: false,
            },
            waiting_data: {
                show: false
            },
            is_open: true,
        }
    },
    methods: {
        sendSingle(){
            this.is_open = !this.is_open
        },
        confirm() {
            Dialog.confirm({
                title: '',
                message: ' 56 '
              }).then(() => {
                // on confirm
                this.is_open = !this.is_open
              }).catch(() => {
                // on cancel
                this.is_open = !this.is_open
              });
        },
        copy(){
            Toast('');
        }
    }
}


